import {CREATE_STREAM, FETCH_STREAM, FETCH_STREAMS, DELETE_STREAM, EDIT_STREAM} from '../actions/types';
import _ from 'lodash';

const streamReducer = (state = {}, action) =>{
    switch(action.type){
        case EDIT_STREAM:
            return {...state, [action.payload.id]: action.payload};

        case FETCH_STREAM:
            return {...state, [action.payload.id]: action.payload};

        case FETCH_STREAMS:
            return {...state, ..._.mapKeys(action.payload, 'id')};

        case DELETE_STREAM:
            return _.omit(state, action.payload);

        case CREATE_STREAM:
            return {...state, [action.payload.id]: action.payload};

        case "FILTER_STREAMS":
            let temp = (Object.values(state).filter( item =>  action.payload === item.userId));
            return {..._.mapKeys(temp, 'id')};
        
        case "EMPTY":
            return {}

        default:
            return state;
    };
};

export const commentReducer = (state = {}, action) =>{
    switch(action.type){
        case "FETCH_COMMENTS":
            return {..._.mapKeys(action.payload, 'id')};

        case "CREATE_COMMENT":
            return {...state, [action.payload.id]: action.payload};
            
        default:
            return state;
    };
};

export const replyReducer = (state = {}, action) =>{
    switch(action.type){
        case "FETCH_REPLIES":
            return {..._.mapKeys(action.payload, 'id')};

        case "REPLY_COMMENT":
            return {...state, [action.payload.id]: action.payload};
            
        default:
            return state;
    };
};



export default streamReducer;