import React from 'react';
import {Field, reduxForm} from 'redux-form';

class StreamForm extends React.Component {
    renderError(meta) {
        if (meta.touched && meta.error){
            return (
                <div className = "ui error message">
                    <div className = "header">{meta.error}</div>
                </div>
            );
        }

    };


    renderInput = (formProps) => {
        const className = `field ${formProps.meta.error && formProps.meta.touched ? 'error' : ''}`;
        return (
            <div className = {className}>
                <label>{formProps.label}</label>
                <textarea rows = {formProps.rows}{...formProps.input} autoComplete = "off"/>
                {this.renderError(formProps.meta)}
            </div>
        );
    };

    onSubmit = (formValues) => {
        this.props.onSubmit(formValues);
    };

    title = () =>{
        if (!this.props.notitle){
            return <Field name = "title" component = {this.renderInput} label = "Enter Title" rows = "1"/>
        }
        else{
            return null;
        }
    }

    render(){
        return (
            <form onSubmit = {this.props.handleSubmit(this.onSubmit)} className="ui form error">
                {this.title()}
                <Field name = "description" component = {this.renderInput} label = "Enter Description" rows = "5"/>
                <button className = "ui button primary fluid">Submit</button>
            </form>
        );
    };

};

const validate = (formValues) =>{
    const errors = {};
   
    if (!formValues.title){
        errors.title = "A Title Is Required"
    } else if (formValues.title.length >50){
        errors.title = "Title Too Long"
    }

    if (!formValues.description){
        errors.description = "A Description Is Required"
    }

    return errors;
};

const streamformexport =  reduxForm({form: 'streamForm', validate})(StreamForm);
export default streamformexport;