import React from 'react';
import {connect} from 'react-redux';
import  {Link} from 'react-router-dom';



import Modal from '../Modal';
import history from '../../history';
import {fetchStream, deleteStream} from '../../actions';

class StreamDelete extends React.Component {
    componentDidMount(){
        //this.props.fetchStream(this.props.match.params.id) //unncessary cause stream list component sets state
     
    };

     renderActions(){
         
         return(
        <React.Fragment> 
            <button onClick={() => this.props.deleteStream(this.props.match.params.id)} className="ui negative button">Delete</button>
            <button onClick= {()=>history.goBack()} className="ui button">Cancel</button>
        </React.Fragment>
         );
     };

     renderContent(){
        if (!this.props.stream){
            return "Loading";
        }

        else{
            return "Are You Sure You Want To Delete: " + this.props.stream.title;
        }
     };

    render(){
        return (
            <div>
                <Modal header="Delete?" content={this.renderContent()} onDismiss={() => history.goBack()} actions = {this.renderActions()}/>
            </div>
    );
    }

};

const mapStateToProps = (state, ownProps) =>{
    return {stream: state.streamreducer[ownProps.match.params.id]}
};

export default connect(mapStateToProps, {fetchStream, deleteStream})(StreamDelete);