import React from 'react';
import {connect} from 'react-redux';
import {Route } from 'react-router-dom';

import Modal from '../Modal';
import history from '../../history';
import StreamShow from './StreamShow';
import StreamForm from './StreamForm';
import {createComment, replyComment} from '../../actions'

class CommentPost extends React.Component {
     

     renderContent(){
         
        if (!this.props.stream){
            return "Loading";
        }

        else{
            return (
                <StreamForm notitle = {true} onSubmit={this.onSubmit} />
            );
        }
     };

     onSubmit = (formValues) => {
        if (this.props.match.path !== "/comment/:id/reply/:replyid"){
            this.props.createComment(formValues, this.props.match.params.id);
        }
        else{
            this.props.replyComment(formValues, this.props.match.params.id, this.props.match.params.replyid);
        }
    };

    render(){
        return (
            <div>
                <Route path = "/comment/:id" exact component = {StreamShow} />
                <Route path = "/comment/:id/reply/:replyid" exact component = {StreamShow} />
                <Modal header="Add Comment" content={this.renderContent()} onDismiss={() => history.push(`/show/${this.props.match.params.id}`)}/>
            </div>
    );
    }

};

const mapStateToProps = (state, ownProps) =>{
    return {stream: state.streamreducer[ownProps.match.params.id]}
};

export default connect(mapStateToProps, {createComment, replyComment})(CommentPost);