import React from 'react';
import {connect} from 'react-redux';

import { filterStreams, fetchStreams, empty } from '../../actions';
import StreamList from './StreamList';



class Profile extends React.Component {
    async fetch(){
        this.props.empty(); 
        await this.props.fetchStreams();    //will allow for state to load so it can then be filtered
        this.props.filterStreams(this.props.match.params.userid); //fiters streams to match id of userid in url
    }

    componentDidMount(){
        this.fetch();
    }

    render(){
        if (!this.props.streams) {return null;}

        return (
            <div>
                <h3>USER: {this.props.match.params.userid}</h3>
                <h3>Total Posts: {this.props.streams.length}</h3>
                <h3>Total Likes: {this.totalLikes()} </h3>
                <StreamList deleteurl = {`/profile/${this.props.match.params.userid}/`}  noButton = {true} filter= {true} />
            </div>
        );
    };

    totalLikes = () =>{
        if (!this.props.streams[0]){return 0;}

        return (this.props.streams.map((stream => {if (!stream.likes) {return 0;} else {return stream.likes.length}})).reduce((a,b) => a+b));
        
    }
};

const mapStateToProps = (state) => {
    return {
        streams: Object.values(state.streamreducer)
    };
};

const profileexport = connect(mapStateToProps, {filterStreams, fetchStreams, empty})(Profile);
export default profileexport;