import React from 'react';
import { Router, Route } from 'react-router-dom';

import StreamCreate from './streams/StreamCreate';
import StreamDelete from './streams/StreamDelete';
import StreamEdit from './streams/StreamEdit';
import StreamList from './streams/StreamList';
import StreamShow from './streams/StreamShow';
import CommentPost from './streams/CommentPost';
import Profile from './streams/StreamProfile';
import Header from './Header'
import history from '../history'
import  '../responsive.css'


const App = () =>{
    
    return (<div>
        <Router history = {history}>
            <div>
                <Header />
                <div style={{display: "flex", justifyContent: "center"}}>
                    <div className = "phone">
                <Route path = {["/", "/delete/:id"]}  exact component = {StreamList} />
                <Route path = {["/profile/:userid", "/profile/:userid/delete/:id"]}  exact component = {Profile} />
                <Route path = "/show/:id" exact component = {StreamShow} />
                <Route path = "/edit/:id" exact component = {StreamEdit} />
                <Route path = "/comment/:id" exact component = {CommentPost} />
                <Route path = "/comment/:id/reply/:replyid" exact component = {CommentPost} />
                <Route path = {["/delete/:id", "/profile/:userid/delete/:id"]} exact component = {StreamDelete} />
                <Route path = "/create" exact component = {StreamCreate} />
                    </div>
                </div>
            </div>    
        </Router>
        </div>
    );
};

export default App;