import React from 'react';
import {connect} from 'react-redux';
import {signIn, signOut} from '../actions';


class GoogleAuth extends React.Component {
    componentDidMount(){
        window.gapi.load('client:auth2', () =>{
            window.gapi.client.init({
                clientId: '1048850885197-bbpfntuqpmjfjckfenech4brhoo6aufm.apps.googleusercontent.com', 
                scope: 'email'
            }).then(()=>{
                this.auth = window.gapi.auth2.getAuthInstance();
                this.onAuthChange(this.auth.isSignedIn.get());
                this.auth.isSignedIn.listen(this.onAuthChange);
            });
        });
    };


    attemptSignIn = () => {
        this.auth.signIn();
    };

    attemptSignOut = () => {
        this.auth.signOut();
    };

    onAuthChange = (isSignedIn) => {
        if (isSignedIn){
            this.props.signIn(this.auth.currentUser.get().getId());
        }
        else {
            this.props.signOut();
        }
    };

    renderAuthButton(){
        if (this.props.isSignedIn === null){
            return null;
        }
        else if (this.props.isSignedIn === true){
            return (
                <button onClick = {this.attemptSignOut} className = "ui red google button">
                    <i className="google icon" />
                    Sign Out
                </button>
            );
        }
        else {
            return (
                <button onClick = {this.attemptSignIn} className = "ui red google button">
                    <i className="google icon" />
                    {this.props.isSignedIn}
                    Sign In
                </button>
            );
        }
    };
    render(){
        return <div>{this.renderAuthButton()}</div>;

    };

};

const mapStateToProps = (state) => {
    return {isSignedIn: state.authreducer.isSignedIn};

};

export default connect(mapStateToProps, {signIn, signOut})(GoogleAuth);