import React from 'react';

import {connect} from 'react-redux';
import {Link} from 'react-router-dom';

import {fetchStream, fetchComments, fetchReplies} from '../../actions';
import LikePost from './LikePost'


class StreamShow extends React.Component {
    componentDidMount(){
        this.props.fetchStream(this.props.match.params.id);
        this.props.fetchComments(this.props.match.params.id);
        this.props.fetchReplies();
    };

    renderAdmin(comment){
        
            return <Link to={`/profile/${comment.userId}`} style={{margin: `0px 0vw 1vh 5vw`}} className="ui top right floated mini label">By: {comment.userId}</Link>;
        
    };

    renderReplies(commentid){
        if (!this.props.replies){return null;}
        return this.props.replies.map(reply => { 
            if (reply.commentId != commentid){return null;}

            return (
                <div className = "item" key = {reply.id}>
                    <br/>
                       
                    <i className = "grey right triangle icon" />
                        
                        <div className = "content">
                            {reply.description}          
                        </div>
                        <div className = "content">
                            {this.renderAdmin(reply)} 
                        </div>
                    </div>
            );
            
        })
    }

    renderComments(){
        if (!this.props.comments) {return null;}
        return this.props.comments.map(comment =>{
            return (
                <div style = {{
                    overflow: "hidden",
                
                    textOverflow: "ellipsis", 
                    wordBreak: "break-word", 
                    display: "-webkit-box", 
                    WebkitBoxOrient: "vertical"
                }}
                className = "item" key = {comment.id}>
                    <div className = "ui divider"></div>

                    <i className = "black right triangle icon" />

                    <div style = {{paddingRight: "1.5vw"}} className = "content">
                        {comment.description} {this.renderReplyButton(comment)}
                    </div>

                     <div style = {{marginRight: "1.5vw"}} className = "content">
                        {this.renderAdmin(comment)}
                    </div>

                    <div style = {{marginTop: "-2.5vh", marginLeft: "2vw", marginRight: "3vw"}} className = "list">
                            {this.renderReplies(comment.id)}
                    </div>
                
                    
                </div>
            );



        })
    }

    renderReplyButton = (comment) =>{
        if (this.props.isSignedIn){
            return <Link to={`/comment/${this.props.match.params.id}/reply/${comment.id}`}style = {{cursor: "pointer"}} className = "ui mini label">Reply</Link>;
        }
        else{
            return <div onClick = {()=> alert("You Must Login To Reply")} style = {{cursor: "pointer"}} className = "ui mini label">Reply</div>;
        }
    }

    renderCreateComment = () =>{
        if (this.props.isSignedIn){
            return (<Link to ={`/comment/${this.props.stream.id}`} className = "ui mini right floated button inverted secondary">
                        Create Comment
                    </Link >);
        }
        else {
            return (<div onClick = {()=> alert("You Must Login Comment")} className = "ui mini right floated button inverted secondary">
            Create Comment
        </div>);
        }

    }


    render(){
        if (!this.props.stream){
            return <div>loading</div>;
        }

        else{
            return (
                <div className = "ui raised segments">
                    
                    <div style={{background: "#e6e6e6"}} className = "ui segment">
                        <div className = "ui list">
                            <div className = "item">
                                <div className = "left floated content">
                                    <LikePost streamid = {this.props.stream.id} streamlikes = {this.props.stream.likes} />
                                </div>
                    
                                
                                <div style = {{wordBreak: "break-word"}} className = "content">
                                    <h1 className = "header">{this.props.stream.title} <Link to={`/profile/${this.props.stream.userId}`} style={{margin: `0px 0vw 1vh 5vw`}} className="ui right floated small label">By: {this.props.stream.userId}</Link></h1>
                                    <br/>
                                    <br/>
                                    <h5 style={{wordBreak: "break-word", whiteSpace: "pre-wrap", margin: "0px 5vw 0px 5vw"}} className = "description">{this.props.stream.description}</h5>
                                    <br/>
                                </div>
                            </div>
                        </div>
                    </div>


                    <div style={{background: "#e6e6e6"}} className = "ui segment">

                        {this.renderCreateComment()}

                        <p>| Comments |</p>
                        <div className = "ui list">
                            {this.renderComments()}
                        </div>
                    </div>
                </div>
            );
            
        };

    };
};

const mapStateToProps = (state, ownProps) => {
    return {stream: state.streamreducer[ownProps.match.params.id],
            currentUserId: state.authreducer.userId,
            isSignedIn: state.authreducer.isSignedIn,
            comments: Object.values(state.commentReducer),
            replies: Object.values(state.replyReducer)
    };
};

export default connect(mapStateToProps, {fetchStream, fetchComments, fetchReplies})(StreamShow);