import React from 'react';
import {Link} from 'react-router-dom';
import GoogleAuth from './GoogleAuth';
import {connect} from 'react-redux';

class Header extends React.Component{
    render(){
        return (
            <div style ={{background: "#e6e6e6"}} className = "ui secondary pointing menu">
                <Link to = "/" className = "item" >Forum</Link>
                <div className = "right menu">
                    {this.renderViewProfile()}
                    <GoogleAuth />
                </div>



            </div>
        );
    }


    renderViewProfile(){
        if (this.props.currentUserId){
            return <Link to = {`/profile/${this.props.currentUserId}`} className = "item">View Profile</Link>;
        }
        else{
            return <div style = {{cursor: "pointer"}} onClick = {()=> alert("You Must Login To View Your Profile")} className = "item">View Profile</div>;
        }
    }
};
const mapStateToProps = (state) => {
    return {
        currentUserId: state.authreducer.userId
    };
};

export default connect(mapStateToProps, null)(Header);