import React from 'react';
import {connect} from 'react-redux';
import {likePost} from '../../actions';

class LikePost extends React.Component {

    calculatelikes(likes){
        if (!likes){
            return 0;
        }
        else {
            return likes.length;
        }
    }
    
    checkLikes(likes){
        if (!likes) {return false;}

        for (let i = 0; i < likes.length; i++){
            if (likes[i] === this.props.currentUserId){
                return true;
            }
        }

        return false;
    }

    editLikes = (id) => {
        
        let item = this.props.streams;
        for (let i = 0; i <item.length; i++){
            if (item[i].id === id){


                if (!item[i].likes){return [this.props.currentUserId];}
                
                for (let j = 0; j <item[i].likes.length; j++){
                    if (item[i].likes[j] === this.props.currentUserId){
       
                        item[i].likes[j] = null;

                        return item[i].likes.filter((user) => user !== null);
                    }

                }
                

                return [...(item[i].likes), this.props.currentUserId];
            }
        }
    }


    render(){
        return <i style = {{cursor: "pointer"}} onClick = {() => {if (this.props.isSignedIn) {this.props.likePost(this.props.streamid, this.props.currentUserId, this.editLikes(this.props.streamid)) } else{alert("You Must Login To Like A Post")}}} className = {`large middle aligned icon heart ${this.checkLikes(this.props.streamlikes)  ? "red" : " outline"}`}> { this.calculatelikes(this.props.streamlikes)}</i>;
    }
}


const mapStateToProps = (state) => {
    return {
            currentUserId: state.authreducer.userId,
            isSignedIn: state.authreducer.isSignedIn,
            streams: Object.values(state.streamreducer)
    };
};

export default connect(mapStateToProps, {likePost})(LikePost);