import React from 'react';
import {connect} from 'react-redux';
import {fetchStreams, empty, likePost, fetchComments} from '../../actions';
import {Link} from 'react-router-dom';
import LikePost from './LikePost';

class StreamList extends React.Component {
    
    componentDidMount(){

        if (!this.props.filter){
            this.props.empty();         //will not be called from the profile component so it will not reset state values
            this.props.fetchStreams();
            
        }
        this.props.fetchComments(-1);
    }
    
    renderAdmin(stream){
        if (stream.userId === this.props.currentUserId){
            
            return (
                <div style={{margin: "0px 0px 1vh 5vw"}} className=" right floated content">
                    <Link to={`/edit/${stream.id}`} className = "ui mini inverted button primary">Edit</Link>
                    <Link to={`${this.props.deleteurl || '/'}delete/${stream.id}`} className = "ui mini button negative">Delete</Link>
                    
                </div>
            );
        }
        else {
            return <Link to={`/profile/${stream.userId}`} style={{margin: "0px 0px 1vh 5vw"}} className="hide label ui top right floated mini label">By: {stream.userId}</Link>;
        }
    };

    renderList(){
        return this.props.streams.map(stream => {
            return(
            <div className = "item" key ={stream.id}>

                {this.renderAdmin(stream)}

                <LikePost streamid = {stream.id} streamlikes = {stream.likes} />

                <Link to= {`/show/${stream.id}`} className = "content">

                    <h3 style={{
                            overflow: "hidden", 
                            textOverflow: "ellipsis", 
                            wordBreak: "break-word",
                            whiteSpace: "pre-wrap",
                            display: "-webkit-box", 
                            WebkitLineClamp: "3", 
                            WebkitBoxOrient: "vertical",
                            marginBottom: "1vh"}}
                        className = "header">

                    {stream.title}
                    </h3>

                    <div className =  "description" 

                        style={{
                            overflow: "hidden", 
                            textOverflow: "ellipsis", 
                            wordBreak: "break-word",                           
                            whiteSpace: "pre-wrap",
                            display: "-webkit-box", 
                            WebkitLineClamp: "3", 
                            WebkitBoxOrient: "vertical",
                            marginBottom: "1vh"}}>

                        <h5>{stream.description}</h5>
                    </div>

                </Link>
            </div>
            );
        });
    };

    renderCreate(){
        if (this.props.isSignedIn && !this.props.noButton){
            return (
                <div>
                    <Link to= "create" className = "ui button inverted secondary fluid"> 
                        Create a Post
                    </Link>
                </div>
            );
        }
        else if (!this.props.noButton){
            return (
                <div>
                    <div onClick = {()=> alert("You Must Login To Create A Post")} to = "/signin" className = "ui button inverted secondary fluid"> 
                        Create a Post
                    </div>
                </div>
            ); 
        }
    };

    

    render(){
        if (!this.props.streams[0]){
            return null;
        } //waits for state to load before rendering ****code will break if 0 posts are on api
        
        
        return (
            <div className = "ui raised segments">
                <div style={{background: "#e6e6e6"}}>
                    <br/>
                    <h1 className = "ui center aligned header">Posts</h1>            
                    {this.renderCreate()}
                    <div className = "ui celled list">{this.renderList()}</div>
                    {this.renderCreate()}
                </div>
            </div>
        );
    };
    
};

const mapStateToProps = (state) => {
    return {
        streams: Object.values(state.streamreducer), 
        currentUserId: state.authreducer.userId,
        isSignedIn: state.authreducer.isSignedIn
    };
};

export default connect(mapStateToProps, {fetchStreams, empty, likePost, fetchComments})(StreamList);