import {SIGN_IN, SIGN_OUT, CREATE_STREAM, FETCH_STREAM, FETCH_STREAMS, DELETE_STREAM, EDIT_STREAM} from './types';
import streams from '../apis/streams';
import history from '../history';


export const signIn = (userId) =>{
    return {
        type: SIGN_IN,
        payload: userId
    };
};

export const signOut = () => {
    return {type: SIGN_OUT};
};

export const empty = () =>{
    return {
        type: "EMPTY"
    };
};

export const createStream = formValues => async (dispatch, getState) => {
    const userId = getState().authreducer.userId;
    const response = await streams.post('/streams', {...formValues, userId});
    

    dispatch( {
        type: CREATE_STREAM,
        payload: response.data
    });

    history.push('/');
};

export const createComment = (formValues, postid) => async (dispatch, getState) => {
    const userId = getState().authreducer.userId;
    const response = await streams.post(`/stream/${postid}/comments`, {...formValues, userId});
    

    dispatch( {
        type: "CREATE_COMMENT",
        payload: response.data
    });

    history.push(`/show/${postid}`);
};

export const replyComment = (formValues, postid, commentid) => async (dispatch, getState) => {
    const userId = getState().authreducer.userId;
    const response = await streams.post(`/comment/${commentid}/replies`, {...formValues, userId});
    
    dispatch( {
        type: "REPLY_COMMENT",
        payload: response.data
    });

    history.push(`/show/${postid}`);
};

export const fetchReplies = () => async (dispatch) => {
    const response = await streams.get('/replies');
    dispatch( {
        type: "FETCH_REPLIES",
        payload: response.data
    });
};

export const fetchStreams = () => async dispatch =>{
    const response = await streams.get('/streams');
    dispatch( {
        type: FETCH_STREAMS,
        payload: response.data
    });
};

export const fetchComments = (postid) => async dispatch =>{
    const response = await streams.get(`/stream/${postid}/comments`);
    dispatch( {
        type: "FETCH_COMMENTS",
        payload: response.data
    });
};

export const fetchStream = (id) => async dispatch =>{
    const response = await streams.get(`/streams/${id}`);

    dispatch( {
        type: FETCH_STREAM,
        payload: response.data
    });
};

export const filterStreams = (userId) =>{
    return{
        type: "FILTER_STREAMS",
        payload: userId
    };

};

export const editStream = (id, formValues) => async dispatch =>{
    const response = await streams.patch(`/streams/${id}`, formValues);
  
    dispatch( {
        type: EDIT_STREAM,
        payload: response.data
    });

    history.push('/');
};

export const deleteStream = (id) => async dispatch =>{
    await streams.delete(`/streams/${id}`);
    dispatch( {
        type: DELETE_STREAM,
        payload: id
    });

    history.goBack();
};

export const likePost = (id, user, data) => async dispatch =>{
    const response = await streams.patch(`/streams/${id}`, {likes: data});

    dispatch( {
        type: FETCH_STREAM,
        payload: response.data
    });
};
